import { Component, OnInit, ViewChild } from '@angular/core';
import { DefinitiveBox } from 'src/app/models/definitive-box.interface';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { DefinitiveBoxService } from '../services/definitive-box.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';

@Component({
	selector: 'app-definitive-boxes-dispatch',
	templateUrl: './definitive-boxes-dispatch.component.html',
	styleUrls: ['./definitive-boxes-dispatch.component.scss']
})
export class DefinitiveBoxesDispatchComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public displayedColumns: string[] = ['codigoExterno', 'quantidadeLotes', 'quantidadeKits', 'dataCadastro', 'dataFehamento', 'usuarioFechamento', 'capaLote'];
	public dataSource = new MatTableDataSource<DefinitiveBox>();
	public form: FormGroup;
	public pouchs: any[] = [];
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public loading: boolean;
	public download: string = '';

	constructor(
		private formBuilder: FormBuilder,
		private definitiveBoxService: DefinitiveBoxService,
		private matSnackBar: MatSnackBar
	) { }

	ngOnInit() {
		this.form = this.formBuilder.group({
			numeroCaixa: ['', Validators.required]
		})
	}

	public addKit(): void {
		const numeroCaixa = this.form.value.numeroCaixa;
		if (numeroCaixa != null || numeroCaixa.trim() != "") {
			const index = this.pouchs.map(el => el.codigoExterno).indexOf(numeroCaixa);
			if (index >= 0) {
				SnackbarComponent.showError(this.matSnackBar, null, "A Caixa j foi adicionada");
			} else {
				this.loading = true;
				this.definitiveBoxService.getBox(numeroCaixa).subscribe(
					response => {
						this.loading = false;
						this.pouchs.push(response.data);
						this.dataSource = new MatTableDataSource<DefinitiveBox>(this.pouchs);
					},
					error => {
						this.loading = false;
						SnackbarComponent.showError(this.matSnackBar, error);
					}
				)
			}
		}
		this.form.reset();
		this.download = '';
	}

	public salvar(): void {
		this.loading = true;
		let caixasDefinitivas: string[] = [];
		 this.pouchs.forEach(element => {
			 caixasDefinitivas.push(element.codigoExterno);
		});
		this.definitiveBoxService.setCaixasDespachadas(caixasDefinitivas).subscribe(
			response => {
				this.loading = false;
				this.pouchs = [];
				this.download = response.data.toString();
				this.dataSource = new MatTableDataSource<DefinitiveBox>(this.pouchs);
				SnackbarComponent.showSuccess(this.matSnackBar);
			},
			error => {
				this.loading = false;
				SnackbarComponent.showError(this.matSnackBar, null, error.message);
			}
		)
	}

	public deletar(codigoExterno: string) {
		this.pouchs = this.pouchs.filter(element=>element.codigoExterno != codigoExterno);
		this.dataSource = new MatTableDataSource<DefinitiveBox>(this.pouchs);
	}
	
}
