import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { ResponseList } from 'src/app/models/response-list.interface';

@Injectable({
	providedIn: 'root'
})
export class TemporaryBoxesService {
	private urlTemporarias = "/api/v1/caixas-temporarias";
	constructor (
		private http: HttpClient
	) { }
	

	public getTemporaryBoxes(pageIndex: string, pageSize: string, codigoExterno: string, exportaXLSX: boolean): Observable<ResponseList> {
		let data = {
			PageIndex : pageIndex,
			PageSize : pageSize,
			codigoExterno: codigoExterno,
			exportaXLSX: exportaXLSX
		};

		let options = {}
		if (exportaXLSX){
			options = {responseType : 'blob' as 'json'}
		}

		return this.http.post<any>(environment.urlServices + this.urlTemporarias + '/consulta', data, options);
	}
	
}
