import { Component, OnInit, ViewChild, AfterViewInit, ElementRef, Input } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatDialog } from '@angular/material';
import { DevolutionKitsService } from '../services/devolution-kits.service';
import { IKit } from 'src/app/models/kit.interface';
import { PageEvent } from '@angular/material/paginator';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';
import * as moment from 'moment';
import { DialogTagsNumberComponent } from '../shared/dialog-tags-number/dialog-tags-number.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
	selector: 'app-devolution-of-kits',
	templateUrl: './devolution-of-kits.component.html',
	styleUrls: ['./devolution-of-kits.component.scss']
})

export class DevolutionOfKitsComponent implements OnInit{

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public loading: boolean = false;
	public displayedColumns: string[] = ['item', 'etiqueta', 'status', 'agencia', 'actions'];
	public dataSource = new MatTableDataSource<IKit>();
	public kits: IKit[] = [];
	public length: number = 0;
	public devolutionGroupKits: boolean = false;
	public pageSize = 50;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public agencia: string;
	public etiqueta: string;
	public formDefinitivas: FormGroup;

	constructor (
		private devolutionKitsService: DevolutionKitsService,
		public dialog: MatDialog,
		private matSnackBar: MatSnackBar
	) { }
	
	ngOnInit() {
		this.getKits();
	}

	public getKits(): void {
		this.loading = true;
		this.devolutionKitsService.getKitsToDevolution((this.pageIndex + 1).toString(), this.pageSize.toString()).subscribe(
			response => {
				this.kits = [];
				this.kits = [...response.data];
				this.kits.forEach((element, index) => {
					element['position'] = (index + 1);
				})
				this.devolutionGroupKits = false;
				this.length = response.length;
				this.dataSource = new MatTableDataSource<IKit>(this.kits);
				this.loading = false;
			},
			() => {
				this.loading = false;
			}
		)
	}

	public getKitsAgencia(): void {
		this.loading = true;
		this.devolutionKitsService.getKitsAgenciaToDevolution((this.pageIndex + 1).toString(), this.pageSize.toString(), this.agencia).subscribe(
			response => {
				this.kits = [];
				this.kits = [...response.data];
				this.length = response.length;
				this.kits.forEach((element, index) => {
					element['position'] = (index + 1);
				})
				this.devolutionGroupKits = true;
				this.dataSource = new MatTableDataSource<IKit>(this.kits);
				this.loading = false;
			},
			() => {
				this.loading = false;
			}
		)
	}

	public getKitsEtiqueta(): void {
		this.loading = true;
		this.devolutionKitsService.getKitsEtiquetaToDevolution((this.pageIndex + 1).toString(), this.pageSize.toString(), this.etiqueta).subscribe(
			response => {
				this.kits = [];
				this.kits = [...response.data];
				this.length = response.length;
				this.kits.forEach((element, index) => {
					element['position'] = (index + 1);
				})
				this.devolutionGroupKits = true;
				this.dataSource = new MatTableDataSource<IKit>(this.kits);
				this.loading = false;
			},
			() => {
				this.loading = false;
			}
		)
	}	

	public getKitFiltro(): void {
		this.pageIndex = 0;
		this.pageSize = 50;

		if(this.agencia.length > 2) {
			this.getKitsAgencia();
		} else if(this.agencia.length == 0) {
			this.getKits();
		}
	}


	public getKitEtiqueta(): void {

		if(this.etiqueta.length > 2) {
			this.pageIndex = 0;
			this.pageSize = 1;					
			this.getKitsEtiqueta();
		} else if(this.etiqueta.length == 0) {
			this.pageIndex = 0;
			this.pageSize = 50;					
			this.getKits();
		}
	}


	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getKits();
	}

	public returnGroupKits(): void {
		this.returnToAgency(this.kits);
	}

	public returnToAgency(kits: IKit[]): void {
		const dialogRef = this.dialog.open(DialogTagsNumberComponent, {
			width: '600px',
			height: '90%',
			data: {
				title : "Validar Kits para Devoluo",
				message : "Leia o nmero da etiqueta do(s) processo(s) que quer remover:",
				type : DialogTypes.CONFIRM_TAG_NUMBER,
				tagIds : kits.map(x=> `etiqueta ${x.etiqueta}`)
			}
		});
		
		dialogRef.afterClosed().subscribe(result => {
			if(result == undefined || result.length < 1) {
				SnackbarComponent.showAlert(this.matSnackBar,'Cancelada pelo Usurio');
			} else {
				const tagIDsReturned: string[] = result;
				let etiquetas: string[] = [];
				let valid: boolean = true;
				let message: string = null;
				let repeatedValues = tagIDsReturned.filter((item, i) => tagIDsReturned.indexOf(item) !== i);
				if(repeatedValues.length > 0) {
					message = 'No  possivel cadastrar Etiquetas repeditas: ' + repeatedValues;
					valid = false;
				} else if( tagIDsReturned.filter(item => item === "").length !== 0 ) {
					message = 'Digite todas Etiquetas';
					valid = false;
				} else {
					for(let kit of kits) {
						etiquetas.push(kit.etiqueta);
					}
					let diferentValues: string[] = [];
					diferentValues = tagIDsReturned.filter(item => etiquetas.indexOf(item) < 0);
					if( diferentValues.length > 0 ) {
						message = 'Kits cadastrados difetentes dos listados para devoluo: ' + diferentValues;
						valid = false;
					}
				}
				if (valid) {
					this.successMessage(etiquetas);
				} else {
					SnackbarComponent.showAlert(this.matSnackBar, message);
				}
			}
		});
	}

	private successMessage(etiquetas: string[]): void {
 		const dialogRef = this.dialog.open(DialogTagsNumberComponent, {
			width: '600px',
			maxHeight: '90%',
			data: {
				title : "Malote",
				message : "Digite o nmero do Malote e o Nmero do Lacre para devoluo dos Kits",
				type : DialogTypes.CONFIRM_TAG_NUMBER,
				tagIds : ['Malote','Lacre']
			}
		});
		dialogRef.afterClosed().subscribe(result => {
			if( result == undefined || result.length < 1 ) {
				SnackbarComponent.showAlert(this.matSnackBar,'Cancelada pelo Usurio');
			} else {
				this.loading = true;
				this.devolutionKitsService.putTrasferirKitCaixaDefinitiva(result[0], result[1], etiquetas).subscribe(
					response => {
						this.loading = false;
						if (response.type == "application/octet-stream"){
							SnackbarComponent.showSuccess(this.matSnackBar, 'Malotes devolvido com Sucesso');
							this.pageIndex = 0;
							this.pageSize = 50;							
							this.getKits();	
							
							var dateToday = moment().format('YYYYMMDDHHmmss');
							var fileName = `capa_devolucao_${result[0]}_${dateToday}.pdf`;
							var linkArquivo = window.URL.createObjectURL(response);
							var link = document.createElement("a");
							link.setAttribute("href", linkArquivo);
							link.setAttribute("download", fileName);
							link.style.display = "none";
							document.body.appendChild(link);
							link.click();
							document.body.removeChild(link);
							this.length = null;


						}						
						
					},
					() => {
						this.loading = false;						
						SnackbarComponent.showError(this.matSnackBar);
					}
				);
			}
		});
	}
}
