import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatDialog } from '@angular/material';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { GeneralInformationsService } from 'src/app/services/general-informations.service';
import { TypeDocumentsToArchive } from 'src/app/models/type-documents-archive.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';
import { DefinitiveBoxService } from 'src/app/modules/expedition/services/definitive-box.service';
import { ActivatedRoute } from '@angular/router';
import { CaixaDefinitiva, CapaLote, Kit } from 'src/app/models/caixa-definitiva-capa-lote.interface';
import { Router } from '@angular/router';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { forkJoin } from 'rxjs';
import { element } from 'protractor';


@Component({
	selector: 'app-capa-lote',
	templateUrl: './capa-lote.component.html',
	styleUrls: ['./capa-lote.component.scss']
})
export class CapaLoteComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public formCaixa: FormGroup;
	public tagNumber = new FormControl('')
	public typeDocumentsToArchive: TypeDocumentsToArchive[] = [];
	public qtdeKits: number = 0;
	public showNotUniqueError: boolean;
	public numeroCaixa: string = null;
	public messageKit: string = null;
	public caixaDefinitiva: CaixaDefinitiva = null;
	public capaLotes: CapaLote[] = [];
	public kits: Kit[] = [];
	public gruposKits: CapaLote[] = [];
	public qtd: number = 30;
	public quantidadeKits: number = 0;
	public totalkitsCapaLote: number = 0;
	public loading: boolean = false;
	public documento: number = 0;
	public maximoPermitido = 120;

	constructor(
		private formBuilder: FormBuilder,
		private generalInformation: GeneralInformationsService,
		private definitiveBoxService: DefinitiveBoxService,
		private activatedRoute: ActivatedRoute,
		public dialog: MatDialog,
		private matSnackBar: MatSnackBar,
		private router: Router
	) {

	}

	ngOnInit() {
		this.numeroCaixa = this.activatedRoute.snapshot.paramMap.get('numeroCaixa');
		this.formCaixa = this.formBuilder.group({
			numeroCaixa: ['', Validators.required],
			documento: ['', Validators.required]
		});
		if (this.numeroCaixa != null && this.numeroCaixa != undefined) {
			this.getCapaLote();
		} else {
			this.getDocuments();
		}

	}


	public getCapaLote() {
		this.loading = true;
		forkJoin(
			this.generalInformation.getTypeOfDocuments(),
			this.definitiveBoxService.getCapaLote(this.numeroCaixa)
		).subscribe(([responseDocuments, responseDefinitiveBox]) => {
			this.typeDocumentsToArchive = [...responseDocuments.data];
			this.caixaDefinitiva = responseDefinitiveBox.data;
			this.formCaixa.get('numeroCaixa').setValue(this.caixaDefinitiva.codigoExterno);
			const documento: TypeDocumentsToArchive = this.typeDocumentsToArchive.find(element => element.nome == this.caixaDefinitiva.tipoDocumento);
			this.formCaixa.get('documento').setValue(documento.id);
			this.formCaixa.controls['numeroCaixa'].disable();
			this.formCaixa.controls['documento'].disable();
			this.quantidadeKits = this.caixaDefinitiva.quantidadeKits;
			this.totalkitsCapaLote = 0;
			this.caixaDefinitiva.capaLotes.forEach(element => {
				this.totalkitsCapaLote += element.kits.length;
			});
			this.loading = false;
			this.montarGrupo();
		},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loading = false
			}
		)
	}

	public getDocuments() {
		this.generalInformation.getTypeOfDocuments().subscribe(
			response => {
				this.typeDocumentsToArchive = [...response.data];
				this.formCaixa.get("documento").setValue(5);
				this.maximoPermitido = 120;
			}
		);
	}

	public addKit(): void {
		if (this.tagNumber.value > 8) {
			if (this.qtdeKits + this.totalkitsCapaLote > 1000) {
				SnackbarComponent.showError(this.matSnackBar, null, "Quantidade mxima de 1000 kits pro caixa definitiva");
			}
			if (this.kits.findIndex(item => item.etiqueta == this.tagNumber.value) >= 0) {
				SnackbarComponent.showError(this.matSnackBar, null, "Kit j cadastrado");
			} else {
				this.definitiveBoxService.getDadosKit(this.numeroCaixa, this.tagNumber.value).subscribe(
					response => {
						if (!response.data.pertenceCapaLote) {
							let kit: Kit = response.data;
							this.qtdeKits++;
							this.kits.push(kit);
							this.montarGrupo();
						} else {
							SnackbarComponent.showError(this.matSnackBar, null, "Kit j cadastrado");
						}
					},
					errorResponse => {
						SnackbarComponent.showError(this.matSnackBar, null, errorResponse.error.message);
					}
				)
			}
			this.tagNumber.reset();
		}
	}

	public montarGrupo() {
		let ordemGrupo = null;
		const lastGrupo = this.caixaDefinitiva.capaLotes.length;
		this.kits.forEach((element, i) => {
			if (i % this.qtd == 0) {
				ordemGrupo = (i / this.qtd) + 1;
			}
			element.lote = ordemGrupo;
		});
		const lotes = [...new Set(this.kits.map((element) => element.lote))];
		let arrGrupos = [];
		lotes.forEach(lote => {
			const tempKits = this.kits.filter(element => element.lote == lote);
			const tempQtdKits = tempKits.length;
			const tempLote = (lote + lastGrupo);
			arrGrupos.push({ lote: tempLote, kits: tempKits, validaLote: this.validaGrupoLote(lote, tempQtdKits) });
		});
		this.gruposKits = arrGrupos;
	}

	public montarCapaLote(_lote: number) {
		const capaLote = { lote: _lote, kits: this.kits.filter(element => element.lote == _lote) }
		this.caixaDefinitiva.capaLotes.push(capaLote);
		this.kits = this.kits.filter(element => element.lote != _lote)
	}

	public removeKit(idKit: string) {
		if (idKit) {
			this.kits = this.kits.filter(element => element.etiqueta !== idKit);
			this.qtdeKits--;
			this.montarGrupo();
		}
	}

	public validaGrupoLote(lote: number, qtdKits: number): boolean {
		const qtdLotes = this.caixaDefinitiva.capaLotes.length;
		const totalBruto = this.totalkitsCapaLote + this.qtdeKits;

		if ((lote == 1 && qtdLotes == 0) && qtdKits < this.qtd) {
			return true;
		}
		if (qtdKits == this.qtd) {
			return false
		}

		if (totalBruto == this.quantidadeKits && lote == 1) {
			return false;
		}

		return true;
	}

	public cadastrarLote(lote: number) {
		const grupoKit = this.gruposKits.find(element => element.lote == lote);
		let indice = (this.caixaDefinitiva.capaLotes.length) * this.qtd;
		delete grupoKit.validaLote;
		const newLote = this.caixaDefinitiva.capaLotes.length + 1
		grupoKit.lote = newLote;
		grupoKit.kits = grupoKit.kits.filter(element => {
			indice++;
			element.indice = indice;
			delete element["capaLoteId"];
			delete element["id"];
			delete element["lote"];
			delete element["pertenceCapaLote"];
			return element;
		});
		this.loading = true;
		this.definitiveBoxService.postLotes(this.numeroCaixa, grupoKit).subscribe(
			() => {
				this.loading = false;
				this.lotePDF(newLote.toString());
				grupoKit.kits.forEach(element => {
					this.kits = this.kits.filter(item => item.etiqueta != element.etiqueta);
					this.qtdeKits--;
				});
				this.getCapaLote();
			},
			responseError => {
				SnackbarComponent.showError(this.matSnackBar, null, responseError.error.message);
				this.loading = false;
			}
		)
	}

	public lotePDF(lote: string) {
		this.definitiveBoxService.gerarCapaPDF(this.numeroCaixa, lote).subscribe(
			response => {
				const blob = new Blob([response.body], { type: 'application/octet-stream' });
				const linkArquivo = window.URL.createObjectURL(blob);
				const fileName = response.headers.get('content-disposition').split(';')[1].split('filename')[1].split('=')[1].trim();
				var link = document.createElement("a");
				link.setAttribute("href", linkArquivo.toString());
				link.setAttribute("download", fileName);
				link.style.display = "none";
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
				this.loading = false;
			},
			error => {
				if (error.status !== 404) {
					SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados. Se o erro persistir, entre em contato com o suporte.");
				}
				this.loading = false;
			}
		)
	}

	public encerramento(): void {
		if( (this.qtdeKits + this.totalkitsCapaLote) >= this.maximoPermitido) {
			if (this.gruposKits.length > 0) {
				//encerrar e fechar ultimo lote
				const dialogRef = this.dialog.open(DialogComponent, {
					width: '600px',
					data: {
						title: "Finalizar Cadastro",
						message: "Esse procedimento cadastra o ltimo lote pendente e fecha a caixa definitivamente, sem possibilidade de alterao. Deseja prosseguir?",
						type: DialogTypes.CONFIRMATION
					}
				});
				dialogRef.afterClosed().subscribe(result => {
					if (result == undefined || result.length < 1 || result.length == 'cancel') {
						SnackbarComponent.showAlert(this.matSnackBar, 'Cancelada pelo Usurio');
					} else if (result == 'ok') {
						this.setUltimoLoteEncerrar();
					}
				});
			} else {
				//encerrar lotes fechados
				const dialogRef = this.dialog.open(DialogComponent, {
					width: '600px',
					data: {
						title: "Encerrar Caixa",
						message: "Esse procedimento fecha a caixa definitivamente, sem possibilidade de alterao. Deseja prosseguir?",
						type: DialogTypes.CONFIRMATION
					}
				});
				dialogRef.afterClosed().subscribe(result => {
					if (result == undefined || result.length < 1 || result.length == 'cancel') {
						SnackbarComponent.showAlert(this.matSnackBar, 'Cancelada pelo Usurio');
					} else if (result == 'ok') {
						this.encerrarCaixa();
					}
				});
			}
		} else {
			SnackbarComponent.showAlert(this.matSnackBar, ` necessrio cadastrar no mnimo ${this.maximoPermitido} kits para encerrar a caixa`);
		}	
	}

	private setUltimoLoteEncerrar(): void {
		const grupoKit = this.gruposKits[this.gruposKits.length-1];
		let indice = (this.caixaDefinitiva.capaLotes.length) * this.qtd;
		delete grupoKit.validaLote;
		const newLote = this.caixaDefinitiva.capaLotes.length + 1
		grupoKit.lote = newLote;
		grupoKit.kits = grupoKit.kits.filter(element => {
			indice++;
			element.indice = indice;
			delete element["capaLoteId"];
			delete element["id"];
			delete element["lote"];
			delete element["pertenceCapaLote"];
			return element;
		});
		this.loading = true;
		this.definitiveBoxService.postLotes(this.numeroCaixa, grupoKit).subscribe(
			() => {
				this.loading = false;
				this.lotePDF(newLote.toString());
				this.encerrarCaixa();
			},
			responseError => {
				SnackbarComponent.showError(this.matSnackBar, null, responseError.error.message);
				this.loading = false;
			}
		)
		
	}

	private encerrarCaixa(): void {
		this.loading = true;
		this.definitiveBoxService.putEncerrarCaixa(this.numeroCaixa).subscribe(
			() => {
				this.loading = false;
				this.router.navigate(['expedicao', 'caixas-definitivas-encerradas',this.numeroCaixa.trim(),'detalhes']);
			},
			error => {
				this.loading = false;
				if (error.status !== 404) {
					SnackbarComponent.showError(this.matSnackBar, null, "Erro ao cadastrar a caixa. Se o erro persistir, entre em contato com o suporte.");
				}
			}
		)
	}

	public finalizarCadastro(): boolean {
		if (this.totalkitsCapaLote >= 30 && this.totalkitsCapaLote <= 1000 && this.qtdeKits < 30) {
			return true;
		}

		return false;
	}

	public addCaixaDefinitiva() {
		if (this.formCaixa.valid) {
			const form = this.formCaixa.value;
			let numeroCaixa: string = null;
			if (this.numeroCaixa != null) {
				numeroCaixa = this.numeroCaixa;
			} else {
				numeroCaixa = form.numeroCaixa;
			}
			let numeroDocumento: number = 0;
			if (this.documento != 0) {
				numeroDocumento = this.documento;
			} else {
				numeroDocumento = form.documento;
			}
			this.loading = true;
			this.definitiveBoxService.postKitsCaixa(numeroCaixa, numeroDocumento, []).subscribe(
				response => {
					const caixa = response.data;
					this.caixaDefinitiva = {
						caixaDefinitivaId: caixa.caixaDefinitivaId,
						caixaDefinitivaStatus: caixa.caixaDefinitivaStatus,
						capaLotes: [],
						codigoExterno: caixa.codigoExterno,
						quantidadeKits: 0,
						tipoDocumento: caixa.tipoDocumento
					}
					this.numeroCaixa = caixa.codigoExterno;
					this.loading = false;
					this.tagNumber.reset();
					this.formCaixa.disable();
				},
				error => {
					this.loading = false;
					SnackbarComponent.showError(this.matSnackBar, null, error.error.message);
				}
			);
		}
	}

	public setDocumento() {
		if (Number(this.formCaixa.value.documento) == 5) {
			this.maximoPermitido = 120;
		} else {
			this.maximoPermitido = 250;
		}
	}
}
