import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { TemporaryBoxesService } from 'src/app/modules/expedition/services/temporary-boxes.service';
import { PageEvent } from '@angular/material/paginator';
import * as moment from 'moment';



@Component({
	selector: 'app-temporary-boxes',
	templateUrl: './temporary-boxes.component.html',
	styleUrls: ['./temporary-boxes.component.scss']
})
export class TemporaryBoxesComponent implements OnInit {

	public displayedColumns: string[] = ['item', 'tagNumber', 'classifieds', 'pendings', 'date', 'actions'];
	public dataSource = new MatTableDataSource<any>([]);
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public pouchs: any[] = [];
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public etiqueta: string = '';
	public loading: boolean = false;

	constructor(private temporaryBoxesService: TemporaryBoxesService) { }

	ngOnInit() {
		this.getBoxes();
	}

	public getClass(element): string {
		if (element.quantidadeAprovados > 0 && element.quantidadePendentes === 0) {
			return 'aproved';
		} else if (element.quantidadePendentes > 0) {
			return 'withReproveds';
		}
		return
	}


	public getBoxes(reset: boolean = false, exportaXLSX: boolean = false): void {
		this.pouchs = [];
		this.loading = true;
		if (reset) {
			this.pageIndex = 0;
			this.pageSize = 10;
		}
		this.temporaryBoxesService.getTemporaryBoxes((this.pageIndex + 1).toString(), this.pageSize.toString(), this.etiqueta, exportaXLSX).subscribe(
			response => {
				if (response.type == "application/octet-stream") {
					var dateToday = moment().format('YYYYMMDDHHmmss');
					var fileName = "caixatemporaria" + "_ " + dateToday + ".xlsx";
					var linkArquivo = window.URL.createObjectURL(response);
					var link = document.createElement("a");
					link.setAttribute("href", linkArquivo);
					link.setAttribute("download", fileName);
					link.style.display = "none";
					document.body.appendChild(link);
					link.click();
					document.body.removeChild(link);
					this.length = null;
				} else {
					this.pouchs = [...response.data];
					this.length = response.length;
					this.pouchs.forEach((element, index) => {
						element['position'] = index + 1;
					});
					this.dataSource = new MatTableDataSource<any>(this.pouchs);
				}

				this.loading = false;
			},
			() => {
				this.loading = false;
			}
		);
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getBoxes();
	}

	public resetFilter(event: KeyboardEvent) {
		if (this.etiqueta.length == 0 && (event.key == "Backspace" || event.key == "Delete")) {
			this.getBoxes();
		}
	}
}
