import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Dialog } from 'src/app/models/dialog.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
	selector: 'app-dialog-transfer-to-another-box',
	templateUrl: './dialog-transfer-to-another-box.component.html',
	styleUrls: ['./dialog-transfer-to-another-box.component.scss']
})
export class DialogTransferToAnotherBoxComponent implements OnInit {

	public formTransferToAnotherBox: FormGroup;
	
	constructor(
		public dialogRef: MatDialogRef<DialogTransferToAnotherBoxComponent>,
		@Inject(MAT_DIALOG_DATA) public data: Dialog,
		private formBuilder: FormBuilder,
	) { }
	
	ngOnInit() {
		this.formTransferToAnotherBox = this.formBuilder.group({
			etiqueta : [{ value: this.data.etiqueta, disabled:true  }, Validators.required],
			numeroCaixa : ['', [Validators.required, Validators.maxLength(20)]],
		});
	}

	public transferToAnotherBox(): void {
		if(this.formTransferToAnotherBox.valid){
			const result = {
				etiqueta: this.formTransferToAnotherBox.value.etiqueta,
				numeroCaixa: this.formTransferToAnotherBox.value.numeroCaixa,
				valid: true
			}
			this.dialogRef.close(result);
		}
	}
}
