import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSnackBar } from '@angular/material';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { TypeDocumentsToArchive } from 'src/app/models/type-documents-archive.interface';
import { DefinitiveBoxService } from 'src/app/modules/expedition/services/definitive-box.service';
import { ActivatedRoute } from '@angular/router';
import { CaixaDefinitiva, CapaLote ,Kit, CaixaDefinitivaFechada } from 'src/app/models/caixa-definitiva-capa-lote.interface';
import { Router } from '@angular/router';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';

@Component({
	selector: 'app-boxes-closed-details',
	templateUrl: './boxes-closed-details.component.html',
	styleUrls: ['./boxes-closed-details.component.scss']
})
export class BoxesClosedDetails implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public formCaixa: FormGroup;
	public tagNumber = new FormControl('')
	public typeDocumentsToArchive: TypeDocumentsToArchive[] = [];
	public qtdeKits: number = 0;
	public showNotUniqueError: boolean;
	public numeroCaixa: string = null;
	public messageKit: string = null;
	public CaixaDefinitivaFechada: CaixaDefinitivaFechada = null;
	public capaLotes: CapaLote[] = [];
	public kits: Kit[] = [];
	public gruposKits: CapaLote[] = [];
	public qtd: number = 30;
	public quantidadeKits: number = 0;
	public totalkitsCapaLote: number = 0;
	public loading: boolean = false;

	
	constructor (
		private formBuilder: FormBuilder,
		private definitiveBoxService: DefinitiveBoxService,
		private activatedRoute: ActivatedRoute,
		private matSnackBar: MatSnackBar
	) { 
		
	}
	
	ngOnInit() {
		this.numeroCaixa = this.activatedRoute.snapshot.paramMap.get('numeroCaixa');
		this.formCaixa = this.formBuilder.group({
			codigoExterno: [{ value: '', disabled: true}],
			tipoDocumento: [{ value: '', disabled: true }],
			dataFechamento: [{ value: '', disabled: true }],
			quantidadeKits: [{ value: '', disabled: true}],
			quantidadeLotes: [{ value: '', disabled: true }],
			usuarioFechamento: [{ value: '', disabled: true }]
		});
		this.getCapaLote();
	}

	

	public getCapaLote() {
		this.loading = true;
		this.definitiveBoxService.getDetalhesEncerradas(this.numeroCaixa).subscribe(
			response => {
				this.CaixaDefinitivaFechada = response.data;
				this.formCaixa.get('codigoExterno').setValue(this.CaixaDefinitivaFechada.codigoExterno);
				this.formCaixa.get('tipoDocumento').setValue(this.CaixaDefinitivaFechada.tipoDocumento);
				this.formCaixa.get('dataFechamento').setValue(moment(this.CaixaDefinitivaFechada.dataFechamento, "YYYY-MM-DD HH:mm:ss").format("DD/MM/YYYY - HH:mm:ss"));
				this.formCaixa.get('quantidadeKits').setValue(this.CaixaDefinitivaFechada.quantidadeKits);
				this.formCaixa.get('quantidadeLotes').setValue(this.CaixaDefinitivaFechada.quantidadeLotes);
				this.formCaixa.get('usuarioFechamento').setValue(this.CaixaDefinitivaFechada.usuarioFechamento);
				this.loading = false;
		},
		error => {
			if (error.status !== 404) {
				SnackbarComponent.showError(this.matSnackBar, error);
			}
			this.loading = false;
		})
	}

	public lotePDF(lote: string) {
		this.loading = true;
		this.definitiveBoxService.gerarCapaPDF(this.numeroCaixa, lote).subscribe(
			response => {
				const blob = new Blob([response.body], { type: 'application/octet-stream' });
				const linkArquivo = window.URL.createObjectURL(blob);
				const fileName = response.headers.get('content-disposition').split(';')[1].split('filename')[1].split('=')[1].trim();
				var link = document.createElement("a");
				link.setAttribute("href", linkArquivo.toString());
				link.setAttribute("download", fileName);
				link.style.display = "none";
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
				this.loading = false;
			},
			error => {
				if (error.status !== 404) {
					SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados. Se o erro persistir, entre em contato com o suporte.");
				}
				this.loading = false;
			}
		)
	}

}
