import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TemporaryBoxesComponent } from './temporary-boxes/temporary-boxes.component';
import { ConsultTypeOfKitComponent } from './consult-type-of-kit/consult-type-of-kit.component';
import { DefinitiveBoxesComponent } from './definitive-boxes/definitive-boxes.component';
import { DevolutionOfKitsComponent } from './devolution-of-kits/devolution-of-kits.component';
import { TemporaryBoxDetailsComponent } from './temporary-box-details/temporary-box-details.component';
import { DefinitiveBoxDetailsComponent } from './definitive-box-details/definitive-box-details.component';
import { ExpeditionRoutesGuard } from './expedition-routes.guard';
import { TransferRemoveKitsComponent } from './transfer-remove-kits/transfer-remove-kits.component';
import { CapaLoteComponent } from './capa-lote/capa-lote.component';
import { DefinitiveBoxesClosedComponent } from './definitive-boxes-closed/definitive-boxes-closed.component';
import { BoxesClosedDetails } from './boxes-closed-details/boxes-closed-details.component';
import { DefinitiveBoxesDispatchComponent } from './definitive-boxes-dispatch/definitive-boxes-dispatch.component';
import { DefinitiveBoxesDispatchFilesComponent } from './definitive-boxes-dispatch-files/definitive-boxes-dispatch-files.component';

const routes: Routes = [
	{ path : 'caixas-temporarias', component : TemporaryBoxesComponent, canActivate: [ ExpeditionRoutesGuard ]   },
	{ path : 'caixas-temporarias/:id', component : TemporaryBoxDetailsComponent, canActivate: [ ExpeditionRoutesGuard ]   },
	{ path : 'consultar-tipo-do-kit', component : ConsultTypeOfKitComponent, canActivate: [ ExpeditionRoutesGuard ]   },
	{ path : 'criar-caixa-definitiva', component : CapaLoteComponent, canActivate: [ ExpeditionRoutesGuard ]  },
	{ path : 'devolucao-de-kits', component : DevolutionOfKitsComponent, canActivate: [ ExpeditionRoutesGuard ]   },
	{ path : 'caixas-definitivas-em-espera', component : DefinitiveBoxesComponent, canActivate: [ ExpeditionRoutesGuard ]   },
	{ path : 'caixas-definitivas-em-espera/:id', component : DefinitiveBoxDetailsComponent, canActivate: [ ExpeditionRoutesGuard ]   },
	{ path : 'caixas-definitivas-em-espera/transfer-remove/:id', component : TransferRemoveKitsComponent, canActivate: [ ExpeditionRoutesGuard ]  },
	{ path : 'caixas-definitivas-em-espera/:numeroCaixa/capa-lote', component : CapaLoteComponent, canActivate: [ ExpeditionRoutesGuard ]  },
	{ path : 'caixas-definitivas-encerradas', component : DefinitiveBoxesClosedComponent, canActivate: [ ExpeditionRoutesGuard ]  },
	{ path : 'caixas-definitivas-encerradas/:numeroCaixa/detalhes', component : BoxesClosedDetails, canActivate: [ ExpeditionRoutesGuard ]  },
	{ path : 'despachar-caixas-definitivas', component : DefinitiveBoxesDispatchComponent, canActivate: [ ExpeditionRoutesGuard ]  },
	{ path : 'arquivos-caixas-definitivas-despachadas', component : DefinitiveBoxesDispatchFilesComponent, canActivate: [ ExpeditionRoutesGuard ]  }
	
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class ExpeditionRoutingModule { }
