import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource, MatDialog } from '@angular/material';
import { Kit } from 'src/app/models/kit.model';
import { ActivatedRoute } from '@angular/router';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { DefinitiveBoxService } from '../services/definitive-box.service';
import { tap } from 'rxjs/operators';
import { DialogTagsNumberComponent } from '../shared/dialog-tags-number/dialog-tags-number.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';

@Component({
	selector: 'app-definitive-box-details',
	templateUrl: './definitive-box-details.component.html',
	styleUrls: ['./definitive-box-details.component.scss']
})
export class DefinitiveBoxDetailsComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public idDefinitiveBox: string;
	public displayedColumns: string[] = ['item', 'tagNumber', 'type', 'status', 'actions'];
	public dataSource = new MatTableDataSource<Kit>();
	public kits: Kit[] = [];
	public totalItens: number = 0;
	public onlyReproveds: string = "false";
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	
	constructor (
		private activatedRoute: ActivatedRoute,
		public dialog: MatDialog,
		private definitiveBoxService: DefinitiveBoxService
	) { }
	
	ngOnInit() {
		this.idDefinitiveBox = this.activatedRoute.snapshot.paramMap.get('id');
		this.getKits();
	}

	ngAfterViewInit() {
        this.paginator.page.pipe(
			tap(() => this.getKits())
		).subscribe();
    }

	public getKits(): void {
		 this.definitiveBoxService.getKits(this.idDefinitiveBox, this.pageSize.toString(), this.pageIndex.toString()).subscribe(
			response => {
				this.kits = [...response.data];
				this.totalItens = response.length;
				this.kits.forEach((element, index) => {
					element['position'] = (response.pageSize * response.pageIndex) + (index + 1);
				})
				this.dataSource = new MatTableDataSource<Kit>(this.kits);
			}
		);
	}

	public remover(idKit: string): void {
		const dialogRef = this.dialog.open(DialogTagsNumberComponent, {
			width: '600px',
			data: {
				title : "Confirmar Remoo",
				message : "Leia o nmero da etiqueta do(s) processo(s) que quer remover:",
				type : DialogTypes.CONFIRM_TAG_NUMBER,
				tagIds : [idKit]
			}
		});
		
		dialogRef.afterClosed().subscribe(result => {
			if (result) {
				if (idKit === result) {
					this.successMessage();
				} else {
					this.errorMessage();
				}
			}
		});
	}

	private successMessage(): void {
		this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title : "Sucesso",
				message : "O Kit foi removido com sucesso dessa caixa. Acesse para a pgina de devoluo de Kits para dar continuidade ao processo.",
				type : DialogTypes.INFORMATION,
			}
		});
	}

	private errorMessage(): void {
		this.dialog.open(DialogComponent, {
			data: {
				title : "Erro",
				message : "O cdigo do Kit que foi lido  diferente do cdigo do item selecionado.",
				type : DialogTypes.INFORMATION,
			},
			width: '600px'
		});
	}

	public transferToAnotherBox(idKit: string): void {
		this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title : "Transferir Kit de Caixa",
				message : "Leia o nmero da etiqueta do Kit que voc quer transferir e da caixa de destino.",
				type : DialogTypes.TRANSFER_TO_ANOTHER_BOX,
				tagIds : [idKit]
			}
		});
	}
}
