import { Component, OnInit, ViewChild } from '@angular/core';
import { DefinitiveBox } from 'src/app/models/definitive-box.interface';
import { MatTableDataSource, MatPaginator, MatSnackBar } from '@angular/material';
import { DefinitiveBoxService } from '../services/definitive-box.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PageEvent } from '@angular/material/paginator';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';

@Component({
	selector: 'app-definitive-boxes',
	templateUrl: './definitive-boxes.component.html',
	styleUrls: ['./definitive-boxes.component.scss']
})
export class DefinitiveBoxesComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public displayedColumns: string[] = ['item', 'numeroCaixa', 'quantidadeAprovados', 'dataCriacao', 'capaLote'];
	public dataSource = new MatTableDataSource<DefinitiveBox>();
	private definitiveBoxes: DefinitiveBox[] = [];
	public pouchs: DefinitiveBox[] = null;
	public formDefinitivas: FormGroup;
	public maxDate = new Date();
	public period: string = '';
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public documento: string = 'todos';
	public caixaDefinitiva: string;
	public loading: boolean = false;

	constructor(
		private formBuilder: FormBuilder,
		private definitiveBoxService: DefinitiveBoxService,
		private matSnackBar: MatSnackBar
	) { }

	ngOnInit() {
		const dateNow = moment();
		const datePrevious = moment().subtract(7, 'days');

		this.formDefinitivas = this.formBuilder.group({
			dataInicial: [datePrevious.toISOString(), Validators.required],
			dataFinal: [dateNow.toISOString(), Validators.required],
			documento: String,
		});

		this.getCaixasDefinitivas();
	}


	public getCaixasDefinitivas(exportaXLSX: boolean = false) {
		if (this.formDefinitivas.valid) {
			this.loading = true;
			this.documento = 'todos';
			const form = this.formDefinitivas.value;
			this.pouchs = null;
			let datePipe = new DatePipe("en-US");
			const dataInicial: string = datePipe.transform(form.dataInicial, 'yyyy-MM-dd');
			const dataFinal: string = datePipe.transform(form.dataFinal, 'yyyy-MM-dd');
			const pageIndex = (this.pageIndex + 1).toString();
			const pageSize = this.pageSize.toString();
			this.definitiveBoxService.getCaixasDefinitivas(dataInicial, dataFinal, pageSize, pageIndex, exportaXLSX).subscribe(
				response => {
					if (response.type == "application/octet-stream") {
						var dateToday = moment().format('YYYYMMDDHHmmss');
						var fileName = "caixadefinitiva" + "_" + dateToday + ".xlsx";
						var linkArquivo = window.URL.createObjectURL(response);
						var link = document.createElement("a");
						link.setAttribute("href", linkArquivo);
						link.setAttribute("download", fileName);
						link.style.display = "none";
						document.body.appendChild(link);
						link.click();
						document.body.removeChild(link);
						this.length = null;
					} else {
						this.length = response.length;
						this.pouchs = [...response.data];

						this.pouchs.forEach((element, index) => {
							element['position'] = index + 1;
						})
						this.dataSource = new MatTableDataSource<DefinitiveBox>(this.pouchs);
						this.dataSource.paginator = this.paginator;
						//this.formDefinitivas.reset()
					}
					this.loading = false;

				},
				(error) => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loading = false;
				}
			);
		}
	}

	public getCaixaDefinitiva(exportaXLSX: boolean = false) {
		if (this.caixaDefinitiva != undefined) {
			if (this.caixaDefinitiva.trim().length > 3) {
				this.loading = true;
				this.definitiveBoxService.getCaixaDefinitiva(this.caixaDefinitiva, exportaXLSX).subscribe(
					response => {
						if (response.type == "application/octet-stream") {
							var dateToday = moment().format('YYYYMMDDHHmmss');
							var fileName = "caixadefinitiva" + "_ " + dateToday + ".xlsx";
							var linkArquivo = window.URL.createObjectURL(response);
							var link = document.createElement("a");
							link.setAttribute("href", linkArquivo);
							link.setAttribute("download", fileName);
							link.style.display = "none";
							document.body.appendChild(link);
							link.click();
							document.body.removeChild(link);
							this.length = null;
						} else {
							this.length = response.length;
							this.pouchs = [...response.data];
							this.pouchs.forEach((element, index) => {
								element['position'] = index + 1;
							})
							this.dataSource = new MatTableDataSource<DefinitiveBox>(this.pouchs);
							this.dataSource.paginator = this.paginator;
						}
						this.loading = false;
					},
					error => {
						SnackbarComponent.showError(this.matSnackBar, null, error);
						this.loading = false;
					}
				);
			}
		}
	}

	public filtrarStatus() {
		let caixasFiltro = [];
		switch (this.documento) {
			case 'todos':
				caixasFiltro = this.pouchs;
				break;
			case 'Aprovados':
				caixasFiltro = this.pouchs.filter(item => item.quantidadeAprovados > 0 && item.quantidadePendentes == 0 && item.quantidadeReprovados == 0); //Somente com Todos Aprovados
				break;
			case 'DocumentosPendentes':
				caixasFiltro = this.pouchs.filter(item => item.quantidadePendentes > 0); //Com Documentos Pendentes
				break;
			case 'TodosPendentes':
				caixasFiltro = this.pouchs.filter(item => item.quantidadeAprovados == 0 && item.quantidadePendentes > 0 && item.quantidadeReprovados == 0); //Somente com Todos Pendentes
				break;
			case 'Reprovados':
				caixasFiltro = this.pouchs.filter(item => item.quantidadeReprovados > 0); //Com Documentos Reprovados
				break;
			// case 'Cancelados':
			// 	caixasFiltro = this.pouchs.filter(item => item.quantidadeCancelados > 0); //Com Documentos Reprovados
			// 	break;
			default:
				caixasFiltro = this.pouchs;
				break;
		}
		this.dataSource = new MatTableDataSource<DefinitiveBox>(caixasFiltro);
	}

	public getClass(element: DefinitiveBox): string {
		if (element.quantidadeAprovados > 0 && element.quantidadePendentes === 0 && element.quantidadeReprovados === 0) {
			return 'aproved';
		} else if (element.quantidadeReprovados > 0) {
			return 'withReproveds';
		}
		return
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getCaixasDefinitivas();
	}

	public gerarCapa(tagNumber: string): void {

		this.definitiveBoxService.generateCapaLoteDefinitiva(tagNumber).subscribe(
			response => {
				const blob = new Blob([response], { type: 'application/pdf' });
				const linkArquivo = window.URL.createObjectURL(blob);
				const fileName = `capa.pdf`;
				var link = document.createElement("a");
				link.setAttribute("href", linkArquivo.toString());
				link.setAttribute("download", fileName);
				link.style.display = "none";
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
			},
			() => {
			}
		)

	}
}
