import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { ResponseList } from 'src/app/models/response-list.interface';
import { Kit } from 'src/app/models/kit.model';
import { ResponseObject } from 'src/app/models/response-object.interface';

@Injectable()
export class DefinitiveBoxService {
	private definitiveBoxUrl: string = '/api/v1/caixas-definitivas';
	private capaLoteUrl: string = '/api/v1/capa-lote';
	constructor(
		private http: HttpClient,
	) { }

	public postKitsCaixa(caixa: string, documentoId: number, kits: Kit[]): Observable<ResponseObject> {
		const etiquetas = [];
		for (let kit of kits) {
			etiquetas.push(kit.etiqueta);
		}
		const data = {
			caixa: caixa,
			codigoGrupoArquivamento: documentoId,
			kits: etiquetas
		};
		return this.http.post<ResponseObject>(environment.urlServices + this.definitiveBoxUrl, data);
	}

	public putKitsCaixa(caixa: string, kits: Kit[]): Observable<ResponseList> {
		const etiquetas = [];
		for (let kit of kits) {
			etiquetas.push(kit.etiqueta);
		}
		const data = {
			caixa: caixa,
			kits: etiquetas
		};
		return this.http.put<ResponseList>(environment.urlServices + this.definitiveBoxUrl, data);
	}

	public getCaixasDefinitivas(initialDate: string, finalDate: string, pageSize: string, pageIndex: string, exportaXLSX: boolean): Observable<any> {
		const data = {
			DataInicial: initialDate,
			DataFinal: finalDate,
			PageIndex: pageIndex,
			PageSize: pageSize,
			ExportaXLSX: exportaXLSX
		};

		let options = {}
		if (exportaXLSX) {
			options = { responseType: 'blob' as 'json' }
		}

		return this.http.post<any>(environment.urlServices + this.definitiveBoxUrl + '/acompanhamento-caixa-definitiva', data, options);
	}

	public getCaixaDefinitiva(numeroCaixa: string, exportaXLSX: boolean): Observable<any> {
		const data = {
			CodigoExterno: numeroCaixa,
			ExportaXLSX: exportaXLSX
		};

		let options = {}
		if (exportaXLSX) {
			options = { responseType: 'blob' as 'json' }
		}

		return this.http.post<any>(environment.urlServices + this.definitiveBoxUrl + '/acompanhamento-caixa-definitiva', data, options);
	}

	public getKits(caixaDefinitivaId: string, pageSize: string, pageIndex: string): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + '/api/v1/kits/kits-caixa-definitiva', {
			params: {
				codigoCaixaDefinitiva: caixaDefinitivaId,
				PageSize: pageSize,
				PageIndex: pageIndex,
			}
		});
	}

	public putTrasferirKitCaixaDefinitiva(caixa: string, etiquetaKit: string) {
		const data = {
			caixa: caixa,
			etiquetaKit: etiquetaKit
		};
		return this.http.put<ResponseList>(environment.urlServices + this.definitiveBoxUrl + '/transferir-kit', data);
	}

	public putRemoveKitCaixaDefinitiva(caixa: string, etiquetaKit: string) {
		const dataKit = {
			caixa: caixa,
			etiquetaKit: etiquetaKit
		};
		return this.http.put<ResponseList>(environment.urlServices + this.definitiveBoxUrl + '/remover-kit', dataKit);
	}

	public getCapaLote(numeroCaixa: string): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + '/api/v1/caixas-definitivas/capa-lote', {
			params: {
				caixa: numeroCaixa
			}
		});
	}

	public getDadosKit(numeroCaixa: string, etiqueta: string): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + '/api/v1/kits/kit-caixa-definitiva', {
			params: {
				caixa: numeroCaixa,
				etiqueta: etiqueta
			}
		});
	}

	public postLotes(numeroCaixa: string, capaLotes: any) {
		const data = {
			caixa: numeroCaixa,
			capaLotes: [capaLotes]
		};
		return this.http.post<ResponseList>(environment.urlServices + this.capaLoteUrl, data);
	}

	public putEncerrarCaixa(numeroCaixa: string) {
		const data = {
			caixa: numeroCaixa
		};
		return this.http.put<ResponseList>(environment.urlServices + this.definitiveBoxUrl + '/encerrar-caixa-definitiva', data);
	}

	public gerarCapaPDF(numeroCaixa: string, lote: string): Observable<HttpResponse<any>> {
		return this.http.get<any>(environment.urlServices + this.capaLoteUrl + '/gerar-capa-lote', {
			responseType: 'blob' as 'json',
			params: {
				caixa: numeroCaixa,
				lote: lote
			},
			observe: 'response'
		});
	}

	public generateCapaLoteDefinitiva(tagNumber: string): Observable<any> {
		return this.http.get<any>(`${environment.urlServices}/api/v1/caixas-definitivas/${tagNumber}/capa-lote`, {
			responseType: 'blob' as 'json',
			params: {}
		});
	}

	public getEncerradas(initialDate: string, finalDate: string, pageSize: string, pageIndex: string, caixaDefinitiva: string, exportaXLSX: boolean): Observable<ResponseList> {

		const data = {
			DataInicial: initialDate,
			DataFinal: finalDate,
			PageIndex: pageIndex,
			PageSize: pageSize,
			CaixaDefinitiva: caixaDefinitiva,
			ExportaXLSX: exportaXLSX
		}

		let options = {}
		if (exportaXLSX) {
			options = { responseType: 'blob' as 'json' }
		}

		return this.http.post<any>(environment.urlServices + this.definitiveBoxUrl + '/relatorio-caixa-definitiva', data, options);
	}
	

	public getDetalhesEncerradas(numeroCaixa: string): Observable<any> {
		return this.http.get<any>(`${environment.urlServices + this.definitiveBoxUrl}/${numeroCaixa}/relatorio-caixa-definitiva-detalhado`);
	}

	public getBox(numeroCaixa: string): Observable<any> {
		return this.http.get<ResponseList>(environment.urlServices + this.definitiveBoxUrl + "/" + numeroCaixa + '/caixa-definitiva-expedicao');
	}

	public setCaixasDespachadas(caixasDefinitivas: string[]) {
		const data = {
			caixasDefinitivas: caixasDefinitivas
		};
		return this.http.post<ResponseList>(environment.urlServices + this.capaLoteUrl + "/gerar-file-expedicao", data);
	}

	public getFilesCaixasDespachadas(data: any): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.definitiveBoxUrl + '/consultar-arquivo-expedicao', {
			params: data
		});
	}

	public getFile(link: string): Observable<any> {
		return this.http.get<any>(link, {
			responseType: 'blob' as 'json',
			observe: 'response'
		});
	}
}
