import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { ResponseList } from 'src/app/models/response-list.interface';

@Injectable()
export class DevolutionKitsService {
	
	private devolucaoKitUrl = '/api/v1/kits/kits-devolucao';
	constructor(
		private http: HttpClient,
	) { }
	
	public getKitsToDevolution(pageIndex: string, pageSize: string): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.devolucaoKitUrl, {
			params : {
				PageIndex : pageIndex,
				PageSize : pageSize
			}
		});
	}

	public getKitsAgenciaToDevolution(pageIndex: string, pageSize: string, numeroAgencia: string ): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.devolucaoKitUrl, {
			params : {
				NumeroAgencia: numeroAgencia,
				PageSize : pageSize,
				PageIndex : pageIndex
			}
		});
	}

	public getKitsEtiquetaToDevolution(pageIndex: string, pageSize: string, etiqueta: string ): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.devolucaoKitUrl + '/etiqueta', {
			params : {
				Etiqueta: etiqueta,
				PageSize : pageSize,
				PageIndex : pageIndex
			}
		});
	}	

	public putTrasferirKitCaixaDefinitiva(maloteAzul: string, lacre: string, kits: string[]) {
		const data = {
			numeroMaloteDevolucao: maloteAzul,
			numeroLacreDevolucao: lacre,
			kits : kits
		};

		const options = {responseType : 'blob' as 'json'}		

		return this.http.post<ResponseList>(environment.urlServices + '/api/v1/kits/devolver-kit', data, options);
	}
}
