import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { ResponseObject } from 'src/app/models/response-object.interface';

@Injectable()
export class ConsultTypeKitService {

	constructor (
		private http: HttpClient,
	) { }
	

	public get(etiqueta: string): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(`${environment.urlServices}/api/v1/kits/${etiqueta}/consulta`)
	}

	public generateCover(tagNumber: string, nomeCapa: string, uniorgCapa: string): Observable<any> {
		return this.http.get<any>(`${environment.urlServices}/api/v1/kits/${tagNumber}/capa-devolucao`, {
			responseType : 'blob' as 'json',
			params : {
				nomeCapa: nomeCapa,
				uniorgCapa: uniorgCapa
			}
        });
	}
}
