import { Component, OnInit, Inject, ViewChildren, QueryList } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatInput } from '@angular/material';
import { Dialog } from 'src/app/models/dialog.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
	selector: 'app-dialog-tags-number',
	templateUrl: './dialog-tags-number.component.html',
	styleUrls: ['./dialog-tags-number.component.scss']
})
export class DialogTagsNumberComponent implements OnInit {

	public formConfirmTagNumber: FormGroup;
	public arrayTags: string[] = [];
	@ViewChildren(MatInput) inputs: QueryList<MatInput>;
	
	constructor(
		public dialogRef: MatDialogRef<DialogTagsNumberComponent>,
		@Inject(MAT_DIALOG_DATA) public data: Dialog,
		private formBuilder: FormBuilder,
	) { }
	
	ngOnInit() {
		this.arrayTags = [...this.data.tagIds];
		this.formConfirmTagNumber = this.formBuilder.group({})
		this.arrayTags.forEach((field, index) => {
			const control = this.formBuilder.control('', Validators.required);
			this.formConfirmTagNumber.addControl('tag_'+ index, control);
		})
	}

	public focusNextElement(index: number): void {
		const nextInput = this.inputs.find((element, i) => i === (index + 1));

		if (nextInput) {
			nextInput.focus();
		} else {
			let arrayValues: string[] = [];
			this.inputs.map(element => {
				arrayValues = [...arrayValues, element.value];
			})
			this.dialogRef.close(arrayValues);
		}
	}

}
