import { Component, OnInit, ViewChild } from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { PageConfig } from 'src/app/models/page-config';
import { DefinitiveBoxService } from '../services/definitive-box.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';
import { CaixaDefinitivaDespachada } from 'src/app/models/caixa-definitiva-despachada.interface';

@Component({
	selector: 'app-managerial-view',
	templateUrl: './definitive-boxes-dispatch-files.component.html',
	styleUrls: ['./definitive-boxes-dispatch-files.component.scss'],
	animations: [
		trigger('detailExpand', [
			state('collapsed', style({ height: '0px', minHeight: '0' })),
			state('expanded', style({ height: '*' })),
			transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
		]),
	],
})
export class DefinitiveBoxesDispatchFilesComponent implements OnInit {

	@ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
	public loading: boolean = false;
	public dataSource = new MatTableDataSource<CaixaDefinitivaDespachada>();
	public columnsToDisplay = ['caixas', 'file', 'dataGeracaoArquivo', 'usuario', 'urlDownload'];
	public expandedElement: CaixaDefinitivaDespachada;
	public showErrorMessage: boolean = false;
	public filter: string = null;
	public position: number;
	public pageConfig: PageConfig = null;
	public form: FormGroup;
	public formTime: FormGroup;
	public maxDate = new Date();
	public pouchs: CaixaDefinitivaDespachada[] = [];

	constructor(
		private definitiveBoxService: DefinitiveBoxService,
		private formBuilder: FormBuilder,
		private matSnackBar: MatSnackBar
	) { }

	ngOnInit() {
		const dateNow = moment();
		const datePrevious = moment().subtract(7, 'days');

		this.form = this.formBuilder.group({
			caixaDefinitiva: ['', [Validators.required, Validators.min(3)]]
		});

		this.formTime = this.formBuilder.group({
			dataInicial: [datePrevious.toISOString(), [Validators.required]],
			dataFinal: [dateNow.toISOString(), [Validators.required]],

		});
	}

	public expand(position: any) {
		if (this.expandedElement == this.dataSource.data[position]) {
			this.expandedElement = null;
			this.position = null;
		} else {
			this.expandedElement = this.dataSource.data[position];
			this.position = position;
		}
	}

	public applyFilter() {
		this.dataSource.filter = this.filter.trim().toLowerCase();
	}

	public getByCaixa() {
		if (this.form.valid) {
			const data = {
				caixaDefinitiva: this.form.value.caixaDefinitiva
			}
			this.getloteCaixasDespachadas(data, true);
		}
	}

	public getByTime() {
		if (this.formTime.valid) {
			const datePipe = new DatePipe("en-US");
			const dataInicial: string = datePipe.transform(this.formTime.value.dataInicial, 'yyyy-MM-dd');
			const dataFinal: string = datePipe.transform(this.formTime.value.dataFinal, 'yyyy-MM-dd');
			const data = {
				dataInicial: dataInicial,
				dataFinal: dataFinal
			}
			this.getloteCaixasDespachadas(data, false);
		}
	}

	public getloteCaixasDespachadas(data: any, buscaCaixa: boolean) {
		this.loading = true;
		this.definitiveBoxService.getFilesCaixasDespachadas(data).subscribe(
			response => {
				this.pouchs = response.data;
				this.pouchs.forEach((element, index) => {
					element['position'] = index;
				})
				this.dataSource = new MatTableDataSource<CaixaDefinitivaDespachada>(this.pouchs);
				this.loading = false;
				this.showErrorMessage = false;
				if (buscaCaixa) {
					this.expand(0);
					this.pouchs.forEach((element, index) => {
						element.caixas.forEach(item => {
							item['caixaBuscada'] = (item.caixaDefinitiva == this.form.value.caixaDefinitiva);
						});
					})
				}
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loading = false;
				this.showErrorMessage = true;
			}
		)
	}

}
