import { Component, OnInit } from '@angular/core';
import { ConsultTypeKitService } from '../services/consult-type-of-kit.service';
import { IKit } from 'src/app/models/kit.interface';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { max } from 'rxjs/operators';

@Component({
	selector: 'app-consult-type-of-kit',
	templateUrl: './consult-type-of-kit.component.html',
	styleUrls: ['./consult-type-of-kit.component.scss']
})
export class ConsultTypeOfKitComponent implements OnInit {
	public kit: IKit;
	public etiqueta: string;
	public notFound: boolean = false;
	public loader: boolean = false;
	public form: FormGroup;
	
	constructor (
		private consultService: ConsultTypeKitService,
		private formBuilder: FormBuilder,
	) { }
		
	ngOnInit() {
		this.form = this.formBuilder.group({
			nomeCapa : [''],
			uniorgCapa : ['', [Validators.minLength(7),  Validators.maxLength(7)]]
		});
	}
	
	public get(): void {
		this.loader = true;
		this.kit = null;
		this.notFound = false;
		this.consultService.get(this.etiqueta).subscribe(
			response => {
				this.kit = response.data;
				if (!this.kit){
					this.notFound = true;
				}
				this.loader = false;
			},
			() => {
				this.loader = false;
			}
		);
		this.form.reset();
	}
		
	public keyDownEtiqueta(event): void {
		if (event.keyCode === 113) {
			this.etiqueta = null;
		}
		if ((event.keyCode === 13 || event.keyCode === 118)) {
			this.get();
		}
	}

	public generateCover(tagNumber: string): void {
		this.loader = true;
		if(this.form.valid) {
			this.consultService.generateCover(tagNumber, this.form.value.nomeCapa, this.form.value.uniorgCapa).subscribe(
				response => {
					const blob = new Blob([response], {type: 'application/pdf'});
					const linkArquivo = window.URL.createObjectURL(blob);
					const fileName = `capa_devolucao_kit_etiqueta_${this.kit.etiqueta}.pdf`;
					var link = document.createElement("a");
					link.setAttribute("href", linkArquivo.toString());
					link.setAttribute("download", fileName);
					link.style.display = "none";
					document.body.appendChild(link);
					link.click();
					document.body.removeChild(link);
					this.loader = false;
				},
				() => {
					this.loader = false;
				}
			)
		}
	}

	public openImages(): void {
		const displayImages = {
			document: this.kit.documento,
			images: this.kit.esteiras[0].galleries
		}
		localStorage.setItem('displayImages', JSON.stringify(displayImages));
		window.open('/#/operacao/exibir-imagens');
	}
	
}