import { Component, OnInit } from '@angular/core';
import { IKit } from 'src/app/models/kit.interface';
import { MatTableDataSource, MatDialog } from '@angular/material';
import { DefinitiveBoxService } from '../services/definitive-box.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';
import { ActivatedRoute } from '@angular/router';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { DialogTransferToAnotherBoxComponent } from '../shared/dialog-transfer-to-another-box/dialog-transfer-to-another-box.component';

@Component({
	selector: 'app-transfer-remove-kits',
	templateUrl: './transfer-remove-kits.component.html',
	styleUrls: ['./transfer-remove-kits.component.scss']
})
export class TransferRemoveKitsComponent implements OnInit {
	public loading: boolean;
	public displayedColumns: string[] = ['item', 'etiqueta', 'status', 'acoes'];
	public dataSource = new MatTableDataSource<IKit>();
	public length: number;
	public pageSize: number = 5;
	public pageIndex: number = 0;
	public kits: IKit[] = [];
	public etiqueta: string;
	public status: string;
	public numeroCaixa: string;

	constructor (
		private definitiveBoxService: DefinitiveBoxService,
		public dialog: MatDialog,
		private matSnackBar: MatSnackBar,
		private activatedRoute: ActivatedRoute
	) {  }
	
	ngOnInit() {
		this.numeroCaixa = this.activatedRoute.snapshot.paramMap.get('id');
		this.getKits();
	}

	public changePage(event: any): void {
		this.pageIndex = event.pageIndex;
		this.pageSize = event.pageSize;
		this.getKits();
	}
	

	public filtrarEtiqueta() : void {
		if( this.etiqueta != null ){
			this.kits = this.kits.filter(element => element.etiqueta == this.etiqueta);
			this.dataSource = new MatTableDataSource<IKit>(this.kits);
		}
	}

	public filtrarStatus(): void {
		if ( this.status == 'todos' ) {
			this.dataSource = new MatTableDataSource<IKit>(this.kits);
		} else {
			this.kits = this.kits.filter(element => element.statusResumido == this.status);
			this.dataSource = new MatTableDataSource<IKit>(this.kits);
		}
	}

	public keyDownEtiqueta(): void {
		if(	this.etiqueta == ''){
			this.dataSource = new MatTableDataSource<IKit>(this.kits);
		}
	}

	public getKits(): void {
		this.loading = true;
		
		this.definitiveBoxService.getKits(this.numeroCaixa, this.pageSize.toString(), (this.pageIndex + 1).toString()).subscribe(
			response => {
				this.length = response.length;
				this.kits = [...response.data];
				this.dataSource = new MatTableDataSource<IKit>(this.kits);
				this.kits.forEach((element, index) => {
					element['position'] =  (this.pageSize * this.pageIndex) + (index + 1);
				})
				this.loading = false;
			}
		);
	}

	public getClass(element: IKit): string {
		if (element.statusResumido == 'Aprovado' && !element.capaLote) {
			return 'aproved';
		} else if(element.statusResumido == 'Reprovado' && !element.capaLote){
			return 'withReproveds';
		}
	}

	public transferBox(etiqueta: string): void {
		const dialogRef = this.dialog.open(DialogTransferToAnotherBoxComponent, {
			width: '600px',
			data: {
				title : "Transferir para Outra Caixa",
				message : null,
				type : DialogTypes.TRANSFER_TO_ANOTHER_BOX,
				etiqueta : etiqueta,
				acao: "transfer"
			}
		});
		dialogRef.afterClosed().subscribe(result => {
			if(result == undefined || !result.valid){
				SnackbarComponent.showAlert(this.matSnackBar,'Cancelada pelo Usurio');
			} else if(result.valid) {
				this.definitiveBoxService.putTrasferirKitCaixaDefinitiva(result.numeroCaixa, etiqueta).subscribe(
					() => {
						const menssage: string  = "Transferncia da Etiqueta: " + etiqueta + " registrada com sucesso.";
						SnackbarComponent.showSuccess(this.matSnackBar, menssage);
						this.getKits();
					},
					responseError => {
						SnackbarComponent.showError(this.matSnackBar, responseError, responseError.error);
					}
				);
			}
		});
	}

	public removeKit(etiqueta: string): void {
		const dialogRef = this.dialog.open(DialogTransferToAnotherBoxComponent, {
			width: '600px',
			data: {
				title : "Confirmar Remoo do Kit",
				message : null,
				type : DialogTypes.TRANSFER_TO_ANOTHER_BOX,
				etiqueta : etiqueta,
				acao: "remove"
			}
		});
		dialogRef.afterClosed().subscribe(result => {
			if(result == undefined || !result.valid){
				SnackbarComponent.showAlert(this.matSnackBar,'Cancelada pelo Usurio');
			} else if(result.valid) {
				this.definitiveBoxService.putRemoveKitCaixaDefinitiva(result.numeroCaixa, etiqueta).subscribe(
					() => {
						const messenge: string  = 'Transferncia da Etiqueta: ' + etiqueta + ' registrada com sucesso';
						SnackbarComponent.showSuccess(this.matSnackBar, messenge);
						this.getKits();
					},
					responseError => {
						SnackbarComponent.showError(this.matSnackBar, responseError, responseError.error);
					}
				);
			}
		});
	}
}
